import React from 'react';
import axios from 'axios'

class New extends React.Component {
	state = {unchecked: 0}

componentDidMount() {
	axios.get(`http://qacvmanager.azurewebsites.net/api/cv/status/gray`)
	.then(
	  response => {
		  this.setState({unchecked: response.data.length});
	  });
}

render() {

	return (
			<ul><em className="fa fa-xl fa-file"  style={{color:"#CACACA"}}></em>
			<div className="large">{this.state.unchecked}</div>
			<div className="text-muted">Unchecked</div></ul>
	);
}
}

export default New;