import React from 'react';
import axios from 'axios';

class Flagged extends React.Component {
	state = {flagged: 0}

componentDidMount() {
	axios.get(`http://qacvmanager.azurewebsites.net/api/cv/status/red`)
	.then(
	  response => {
		  this.setState({flagged: response.data.length});
	  });
	}
	render() {

		return (
					<ul><em className="fa fa-xl fa-flag color-red"></em>
						<div className="large">{this.state.flagged}</div>
						<div className="text-muted">Flagged</div></ul>
			
		);
	}
}

export default Flagged;