import React from 'react';
import UserTable from './UserTable.js'
import CVTable from './CVTable.js'
import UploadCv from './UploadCv.js'
import SearchUser from './SearchUser.js'
//import SearchCV from './SearchCV.js'
import axios from 'axios'
import FilterFlag from './FilterFlag.js'
import New from './Figures/New.js'
import Approved from './Figures/Approved.js'
import Flagged from './Figures/Flagged.js'
import {Redirect, Route} from "react-router-dom";

class User extends React.Component {
  state = {
    visiblity: 'hidden'
  }
  componentDidMount() {
    if (this.props.loginInfo[7] !== undefined && this.props.loginInfo[7] !== null)
    {
      if (this.props.loginInfo[7].departmentId=== 1 || this.props.loginInfo[7].departmentId=== 6) {
        axios.get(`https://qacvmanager.azurewebsites.net/api/user/${this.props.loginInfo[0]}/cv`)
          .then(response => {
            this.props.findCv(response.data);
          })
      }
    }
  }
  mainDash = () =>
  {
    if (this.props.loginInfo[7].departmentId === 1 || this.props.loginInfo[7].departmentId=== 6)
    {
      return (this.traineeConsultantDash())
    }
    else
    return (this.trainingManagerDash());

  }
  traineeConsultantDash ()
  {
      return (
      <div>
        <div className="col">
        <span className="txt1 p-b-11">
        Upload CV
        </span>
        <UploadCv userId={this.props.loginInfo[0]} viewerId={this.props.loginInfo[7].departmentId} onClick={this.props.findCv} />
        </div>
        <div className="col">
          <span className="txt1 p-b-11">
          CVs Table
          </span>
          <CVTable loginInfo={this.props.loginInfo} viewerId={this.props.loginInfo[7].departmentId} cvs={this.props.cvs} onClick={this.props.findCv} />
        </div>
      </div>)
  }
  softSkillsDash = () =>
  {
    if (this.props.loginInfo[7].departmentId === 5)
    {
      return (
      <div>
        <div className ="row">
          <span id="loginEmailLabel" className="txt1 p-b-11 col">
            Search User
          </span>
          <span id="loginEmailLabel" className="txt1 p-b-11 col">
            CV Status
          </span>
        </div>
        <div className ="row">
          <div className="col">
						<SearchUser onSubmit={this.props.onSubmit} findUser = {this.props.findUser}/>
            </div>
          <div className="col">
            <FilterFlag onChange={this.props.findCv} cvs={this.props.cvs} />
          </div>
        </div>
      </div>)
    }
    else
    {
      return (
        <div>
          <div className ="row">
            <span id="loginEmailLabel" className="txt1 p-b-11 col">
              Search User
            </span>
          </div>
          <div className ="row">
            <div className="col">
              <SearchUser onSubmit={this.props.onSubmit} findUser = {this.props.findUser}/>
            </div>
          </div>
        </div>)
    }
  }
  trainingManagerDash ()
  {
      return (   <div>
        <div className="panel p-t-20">
          <div className="row">
            <div className="col">
              <div className="panel panel-teal panel-widget border-right">
                <New />
              </div></div>
            <div className="col">
              <div className="panel panel-teal panel-widget border-right">
                <Approved />
              </div></div>
            <div className="col">
              <div className="panel panel-teal panel-widget">
                <Flagged />
              </div></div>
          </div>
        </div>
        <Route component = {this.softSkillsDash}/>
        <div className="row">
            <div className="col">
              <UserTable rows={this.props.rows} onClick={this.props.findCv} cvs={this.props.cvs} />
            </div>
            <div className="col">
              <CVTable loginInfo={this.props.loginInfo} visibility={this.props.visibility} viewerId={this.props.loginInfo[7].departmentId}
                cvs={this.props.cvs} onClick={this.props.findCv}/>
            </div>
        </div>
        </div>)
  }
  render() {
    if (this.props.loginInfo[7] === undefined | this.props.loginInfo[7] === null) {
      return (<Redirect to='/' />)
    }
    else{
      return (
        <div className = "col-sm-9 col-sm-offset-3 col-lg-8 col-lg-offset-3 main m-t-100 m-b-100">
              <span className="login100-form-title p-b-32">
                  Dashboard
              </span>
              <Route component = {this.mainDash}/>
          </div>
      );
    }
  }
}

export default User;