import React from 'react';
import Row from './Row.js'

class UserTable extends React.Component {
    state = { userName: '', userId: '', prevId: '' }
    handleReset = (event) => {
        var empty = [];
        this.props.onClick(empty);
    };

    render() {
        return (
            <table className="table table-hover col">
                <thead>
                    <tr>
                        <th>
                            <div className="txt1 p-b-11">Name</div>
                            <div className="fht-cell"></div>
                        </th>
                        <th>
                            <div className="txt1 p-b-11">Email</div>
                            <div className="fht-cell"></div>
                        </th>
                        <th>
                            <div className="txt1 p-b-11">Role</div>
                            <div className="fht-cell"></div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {this.props.rows.map(row => <Row key={row.userId} onClick={this.props.onClick} cvs={this.props.cvs} prevId={this.state.prevId} {...row} />)}
                </tbody>
            </table>
        );
    }
}
