import React from 'react';
import CreateAccount from './CreateAccount.js'
import {Redirect} from "react-router-dom";

class CreatePage extends React.Component {

    render() {
        if (this.props.loginInfo[7] === undefined | this.props.loginInfo[7] === null) {
            return (<Redirect to='/' />)
        }
        else
        {
            if (this.props.loginInfo[7].departmentId >= 1 && this.props.loginInfo[7].departmentId <= 6) {
                return (
                    <div className = "col-sm-9 col-sm-offset-3 col-lg-8 col-lg-offset-3 main m-t-100 m-b-100">
                            <span className="login100-form-title p-b-32">
                                Create User
                            </span>
                            <CreateAccount viewerId={this.props.loginInfo[7].departmentId} />
                    </div>
                );
            } 
        }
    }
}
export default CreatePage;