import React from 'react';
import axios from 'axios';
import CryptoJS from "crypto-js";

class CreateAccount extends React.Component {
    track = (event) => {
        this.changeStatus(true, "gray", false,"Confirm Account Creation.");
    }
    componentDidMount()
    {
        this.department = document.getElementById("selectDpt");
        this.firstName = document.getElementById("firstName");
        this.lastName = document.getElementById("lastName");
        this.email = document.getElementById("email");
        this.checkBox = document.getElementById("check");
        this.confirm = document.getElementById("confirm");
    }
    createAccount = (event) => {
        event.preventDefault();
        if (this.changeStatus (false)) {
            this.changeStatus(true,"limegreen", false,"Creating account...");
            var defaultPass = "defaultPass";
            axios.post("https://qacvmanager.azurewebsites.net/api/department/" + this.department.value + "/user",
            {
                "firstName": this.capitalizeFirstLetter(this.firstName.value),
                "lastName": this.capitalizeFirstLetter(this.lastName.value),
                "email": this.email.value.toLowerCase(),
                "password": CryptoJS.MD5(defaultPass).toString(),
                "picture" : null,
                "prefLocation" : ""
            }).then((response) =>
            {
                if (response.status === 200) 
                {
                    this.changeStatus(true,"limegreen", false,this.getSelectedOption(this.department).textContent+" account created successfully.");
                    //CODE TO SEND EMAIL ONCE ACCOUNT IS CREATED - DO NOT DELETE 
                    // var data = {
                    //     "from": {"email" : "qacvsinfo@hotmail.com"},
                    //     "personalizations" : [{"to": [{"email":`${email.value}`, "name":`${firstName.value}+" "+${lastName.value}`}]}],
                    //     "subject" : "QACVs Password Reset",
                    //     "content" : [{"type": "text/plain","value": `Hello ${firstName.value},\nWelcome to QACVs. \nThis is your password: "${defaultPass}"\n Please change it once you login"`}]
                    // };
                    // var header = {"Authorization": "Bearer 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",
                    // "Content-Type" : "application/json" };
                    // axios.post("https://cors-anywhere.herokuapp.com/"+"https://api.sendgrid.com/v3/mail/send", data,{headers : header}).then((response)=>
                    // {
                        
                    // })
                }
                this.firstName.value = "";
                this.lastName.value = "";
                this.email.value = "";
            }).catch((error) => {
                this.changeStatus(true,"red", false,"Unable to create account, please try again.");
                this.firstName.value = "";
                this.lastName.value = "";
                this.email.value = "";
                this.firstName.focus();
            })
        }
        else
        {
            if( this.firstName.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter First Name.");
                this.firstName.focus();
            }  
            else if(this.lastName.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter Last Name.");
                this.lastName.focus();
            }
                
            else if(this.email.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter Email.");
                this.email.focus();
            } 
            else
            {
                this.changeStatus(true,"red", false,"Confirm Account Creation.");
                this.checkBox.focus();
            }   
        }
    }
    capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1).toLowerCase();
    }
     getSelectedOption(sel) {
        var opt;
        for ( var i = 0, len = sel.options.length; i < len; i++ ) {
            opt = sel.options[i];
            if ( opt.selected === true ) {
                break;
            }
        }
        return opt;
    }
    changeStatus (change,colour, checked, text){
        if(change)
        {
            this.confirm.style="color:"+colour;
            this.confirm.textContent = text;
            this.checkBox.checked = checked;
        }
        else return (this.checkBox.checked &&  this.email.value !=="" && this.firstName.value !== "" && this.lastName.value !=="");
    }
    render() {
        return (
            <div>
                <form onSubmit={this.createAccount} className="login100-form validate-form flex-sb flex-w">
                <span id="loginEmailLabel" className="txt1 p-b-11">
                   Role
                </span>
                <div className="wrap-input100 m-b-12">
                    <select id="selectDpt" className="input100">
                        <option value="1">Trainee</option>
                        <option value="2">Trainer</option>
                        <option value="3">Training Manager</option>
                        <option value="4">Sales</option>
                        <option value="5">Soft Skills</option>
                        <option value="6">Consultant</option>
                    </select>
                    <span className="focus-input100"></span>
                </div>
                <span id="loginEmailLabel" className="txt1 p-b-11">
                First Name
                </span>
                <div className="wrap-input100 validate-input m-b-12" data-validate="First name is required">
                    <input className="input100" id="firstName" type="text" name="name" placeholder="Enter First Name" onChange = {this.track}/>
                    <span className="focus-input100"></span>
                </div>
                <span id="loginEmailLabel" className="txt1 p-b-11">
                Last Name
                </span>
                <div div className="wrap-input100 validate-input m-b-12" data-validate="Last name is required">
                    <input className="input100" id="lastName" type="text" name="name" placeholder="Enter Last Name" onChange = {this.track}/>
                    <span className="focus-input100"></span>
                </div>
                <span id="loginEmailLabel" className="txt1 p-b-11">
                Email
                </span>
                <div className="wrap-input100 validate-input m-b-12" data-validate="Email is required">
                    <input className="input100" id="email" type="email" name="email" placeholder="Enter Email" onChange = {this.track}/>
                    <span className="focus-input100"></span>
                </div>
                <div className="flex-sb-m w-full p-b-48">
                    <input className="input-checkbox100" id="check" type="checkbox" name="correct-info" />
                    <label className="label-checkbox100" htmlFor="check">
                            <span id = "confirm"  className="txt p-b-11">Confirm Account Creation.</span>
                    </label>
                </div>
                <div className="container-login100-btn" data-validate="Ceck is required">
                    <button id="btnLogin" className="login100-form-btn" type="submit">
                    Create
                    </button>
                </div>
                </form>
            </div>
            );
    }
}

export default CreateAccount;

