import React from 'react';
import axios from 'axios';
import CryptoJS from "crypto-js";
import UploadPhoto from './UploadPhoto.js';

class UpdateInfo extends React.Component {
    track = (event) => {
        this.changeStatus(true, "gray", false,"Confirm preference change.");
    }
    constructor(props) {
        super(props);
        this.baseUrl = "https://qacvmanager.azurewebsites.net/api";
        axios.get(`${this.baseUrl}/user/${this.props.loginInfo[0]}`)
        .then((response) =>
        {
            this.prefLocation.defaultValue = response.data.prefLocation;
        })
        
    }
    componentDidMount()
    {
        this.checkBox = document.getElementById("checkPref");
        this.prefLocation = document.getElementById("location");
        this.confirm = document.getElementById("confirmpPref");
    }
    changeStatus (change,colour, checked, text){
        if(change)
        {
            this.confirm.style="color:"+colour;
            this.confirm.textContent = text;
            this.checkBox.checked = checked;
        }
        else return (this.checkBox.checked &&  this.prefLocation.value !=="");
    }
    capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1).toLowerCase();
    }
    updateLocatioPref = (e) =>
    {
        e.preventDefault();
        if (this.changeStatus(false)) {
            var newLocation = this.capitalizeFirstLetter(this.prefLocation.value);
            this.changeStatus(true,"limegreen", false,"Saving preferred location...");
                axios.put(`${this.baseUrl}/user/${this.props.loginInfo[0]}/prefLocation/${newLocation}`)
                .then(
                  response => {
                    if (response.status === 200) 
                    {
                        this.changeStatus(true,"limegreen", false,"Preferred location saved successfully.");
                        this.prefLocation.value =  newLocation;
                    }
                  })
                .catch((error) => {
                    this.changeStatus(true,"red", false,"Unable to save, please try again");
                    this.prefLocation.focus();
                })
        }
        else
        {
            if(this.prefLocation.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter preferred location.");
                this.prefLocation.focus();
            }  
            else
            {
                this.changeStatus(true,"red", false,"Confirm preference change.");
            }

        }
    }
    render() {
        return (
              <div className="col">
                    <span className="login100-form-title p-b-32">
                        Update Info
                    </span>
                    <span id="loginEmailLabel" className="txt1 p-b-11">
                    Profile Picture
                    </span>
                    <UploadPhoto userId = {this.props.loginInfo[0]} picture = {this.props.loginInfo[5]}/>
                    <div className = "p-b-12 m-t-50 m-b-50">
                        <span id="loginEmailLabel" className="txt1 p-b-12">
                        Location Preferences
                        </span>
                        <div className="wrap-input100 validate-input m-b-12">
                            <input className="input100" id="location" type="text" name="location" placeholder="Enter preferred location" defaultValue = {this.prefLoc} onChange = {this.track}/>
                            <span className="focus-input100"></span>
                        </div>
                        <div>
                            <input  className="input-checkbox100" id="checkPref" type="checkbox" name="correct-info" />
                            <label className="label-checkbox100" htmlFor="checkPref">
                                <span className="lnr lnr-envelope" id = "confirmpPref">Confirm preference change.</span>
                            </label>
                        </div>
                        <div className="container-login100-btn m-t-20" data-validate="Check is required">
                            <button id="btnLogin" className="login100-form-btn" type="submit" onClick = {this.updateLocatioPref}>
                            Update Location
                            </button>
                        </div>
                    </div>
                </div>
        );   
    }
}

export default UpdateInfo;

