import React from 'react';
import axios from 'axios'

class Approved extends React.Component {
	state = {approved: 0}

componentDidMount() {
	this.req = axios.get(`http://qacvmanager.azurewebsites.net/api/cv/status/green`);
	this.req.then(
	  response => {
		  this.setState({approved: response.data.length});
	  });
}
componentWillMount(){
	//this.req.cancel();
  }

	render() {
		return (
				<ul><em className="fa fa-xl fa-thumbs-up" style={{color:"#26ff43"}}></em>
				<div className="large">{this.state.approved}</div>
				<div className="text-muted">Approved</div></ul>
		);
	}
}

export default Approved;