import React, { Component } from "react";
import axios from 'axios';
class Flag extends Component {
    constructor(props) {
        super(props);
        this.statusSelector = null;
        this.setStatusSelector = element => {
            
            this.statusSelector = element;
            if(this.statusSelector != null)
            {
                this.statusSelector.value = this.props.status;
                if (this.props.viewerId === 1 | this.props.viewerId === 6) {
                    this.statusSelector.disabled = true;
                }
            }
        }
        this.updateStatus = (e) => {
            var setStatus = "https://qacvmanager.azurewebsites.net/api/cv/" + this.props.cvId + "/status/";
            this.changeStatus("limegreen","Changing status...");
            axios.get(setStatus + this.statusSelector.value).then(()=>
                {
                    // this.changeStatus("limegreen","Status changed to "+this.statusSelector.value+".");
                    this.changeStatus("limegreen","");
                }
            ).catch((error) => {
                this.changeStatus(true,"red", "Unable to change status.");
            })
        }
    }
    componentDidMount()
    {
        this.confirm = document.getElementById("confirm"+this.props.cvId);
        this.confirm.focus();
    }
    track = (event) => {
        this.changeStatus("gray","");
    }
    changeStatus (colour, text){
        this.confirm.style="color:"+colour;
        this.confirm.textContent = text;
    }
    render() {
        return (
            <div>
                <select onChange={this.updateStatus}  ref={this.setStatusSelector}  >
                    <option value="Gray" onChange = {this.track} >Gray</option>
                    <option value="Green" onChange = {this.track}>Green</option>
                    <option value="Red" onChange = {this.track}>Red</option>
                </select>
                <div>
                    <span id = {"confirm"+this.props.cvId}  className="txt p-b-11"></span>
                </div>
            </div>
        );
    }

}
export default Flag;
