import React from 'react';
import Cvrow from './Cvrow.js'
import UploadCv from './UploadCv.js'
class CVTable extends React.Component {

    constructor(props) {
        super(props);
    }
    state = { userName: '', userId: '', prevId: '' }
    handleReset = (event) => {
        var empty = [];
        this.props.onClick(empty);
    };


    render() {
        return (
            <table className="table table-hover col" style={{ visibility: this.props.visibility }}>
                <thead>
                    <tr>
                        <th>
                            <div className="txt1 p-b-11">File Name</div>
                            <div className="fht-cell"></div>
                        </th>
                        <th>
                            <div className="txt1 p-b-11">Status</div>
                            <div className="fht-cell"></div>
                        </th>
                        <th>
                            <div className="txt1 p-b-11">Download</div>
                            <div className="fht-cell"></div>
                        </th>
                        <th>
                            <div className="txt1 p-b-11">Delete</div>
                            <div className="fht-cell"></div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {this.props.cvs.map(cv => <Cvrow onClick={this.props.onClick} cvs={this.props.cvs} prevId={this.state.prevId} viewerId={this.props.viewerId} key={cv.cvId} findCv={this.props.findCv} {...cv} />)}
                </tbody>
            </table>

        );
    }
}

export default CVTable;


