import React from 'react';
import ChangePassword from './ChangePassword.js'
import UpdateInfo from './UpdateInfo.js'
import {Redirect} from "react-router-dom";

class UpdateInfoPage extends React.Component {

    render() {
        if (this.props.loginInfo[7] !== undefined && this.props.loginInfo[7] !== null) {
            return (
                <div className = "col-sm-9 col-sm-offset-3 col-lg-8 col-lg-offset-3 main m-t-100 m-b-100">
                    <div className="row">
                        <UpdateInfo loginInfo={this.props.loginInfo} />
                    </div>
                    <div className="row m-t-50">
                        <ChangePassword email={this.props.loginInfo[3]} />
                    </div>
                </div>
            );
        } else 
        {
            return (<Redirect to='/' />)
        }
    }
}
export default UpdateInfoPage;