import React from 'react';
import axios from 'axios';

class SearchUser extends React.Component {
  state = {  userName: '' }
  handleSubmit = (event) => {
    event.preventDefault();
    console.log("Search Event: onSubmit", this.state.userName);
    if(this.state.userName !== "")
    {
      axios.get(`https://qacvmanager.azurewebsites.net/api/findbyname/${this.state.userName}&`)
      .then(response => {
        this.props.findUser(response.data);
      })
    }
  };
  
  render() {
    return (
      <div className="form-group col">
          <form onSubmit={this.handleSubmit} className="row">
            <div className="wrap-input100 validate-input m-b-12" data-validate="Email is required" >
              <input type="text" className="input100"
                value = {this.state.userName}
                onChange = {(event) => this.setState({ userName: event.target.value })}
                onKeyUp = {this.handleSubmit}
                placeholder="Enter user" />
                <span className="focus-input100"></span>
            </div>
            <button type="submit" className="login100-form-btn" >Find user</button>
          </form>
      </div>
    );
  }
}

export default SearchUser;