import React, { Component } from "react";
import axios from 'axios';
import CryptoJS from "crypto-js";



export default class LogIn extends Component {
  track = (event) => {
    this.changeStatus(true, "gray","");
  }
  componentDidMount()
  {
      this.password = document.getElementById("loginPasswordfield");
      this.email = document.getElementById("loginemail");
      this.confirm = document.getElementById("confirm");
      this.email.focus();
  }
  changeStatus (change,colour, text){
    if(change)
    {
        this.confirm.style="color:"+colour;
        this.confirm.textContent = text;
    }
    else return (this.email.value !=="" && this.password.value !== "");
  }
  onSubmit = (e) => {
    e.preventDefault();
    if (this.changeStatus (false)) {
      this.changeStatus(true,"limegreen","Logging in...");
      axios.get(`https://qacvmanager.azurewebsites.net/api/login/${this.email.value}&${CryptoJS.MD5(this.password.value).toString()}`)
        .then(
          response => {
            console.log(response.data[0])
            if (response.status === 200) {
              var object = response.data[0];
                 for (var property in object) 
                 {
                  if (object.hasOwnProperty(property)) 
                  {
                    this.props.loginInfo.push(object[property]);
                  }
                }
              this.props.onSubmit(this.props.loginInfo);
              window.location.replace("#/user");
            }
          })
        .catch((error) => {
          console.log(error);
          this.changeStatus(true,"red","Invalid details, please try again.");
          this.email.focus();
        })
    }
    else
    {
      if(this.email.value === "")
      {
          this.changeStatus(true,"red","Please enter email.");
          this.email.focus();
      } 
      else
      {
          this.changeStatus(true,"red","Please enter password.");
          this.password.focus();
      }
    }
  }
  getEmail = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  hashPass = (e) => {
    e.preventDefault();
    this.setState({
      [e.target.name]: e.target.value
      //[e.target.name]: CryptoJS.MD5(e.target.value).toString()
    })
  }
  render() {
    return (
      <div id="login-page">
          <div className="container-login100">
            <div className="wrap-login100 p-l-85 p-r-85 p-t-55 p-b-55">
              <form className="login100-form validate-form flex-sb flex-w"  onSubmit={this.onSubmit}>
                <span className="login100-form-title p-b-32">
                  Account Login
                </span>
                <span id="loginEmailLabel" className="txt1 p-b-11">
                  Email
                </span>
                <div className="wrap-input100 validate-input m-b-36" data-validate="Email is required">
                  <input id="loginemail" className="input100" type="email" name="email" onChange = {this.track}></input>
                  <span className="focus-input100"></span>
                </div>
                <span id="loginPasswordLabel" className="txt1 p-b-11">
                  Password
                </span>
                <div className="wrap-input100 validate-input m-b-12" data-validate="Password is required">
                  <input id="loginPasswordfield" className="input100" type="password" name="passwordInput" onChange = {this.track}></input>
                  <span className="focus-input100"></span>
                </div>
                <div className="flex-sb-m w-full p-b-48">
                  <div className="contact100-form-checkbox">
                    <input className="input-checkbox100" id="ckb1" type="checkbox" name="remember-me"></input>
                    <label className="label-checkbox100" htmlFor="ckb1">
                      Remember me.
							      </label>
                  </div>
                  <div>
                    <a href="#" className="txt3">
                      Forgot Password?
							     </a>
                  </div>
                </div>
                <div className="flex-sb-m w-full p-b-48">
                  <div className="container-login100-form-btn">
                    <button id="btnLogin" className="login100-form-btn">
                      Login
                    </button>
                  </div>
                  <div>
                    <span id = "confirm"  className="txt p-b-11"></span>
                  </div>
                </div>
              </form>
            </div>
          </div>
      </div>
    );
  }
}


