import React from 'react';
import axios from 'axios';
import CryptoJS from "crypto-js";

class ChangePassword extends React.Component {
    track = (event) => {
        this.changeStatus(true, "gray", false,"Confirm password change.");
    }
    componentDidMount()
    {
        this.baseUrl = "https://qacvmanager.azurewebsites.net/api";
        this.checkBox = document.getElementById("check");
        this.email = document.getElementById("email");
        this.oldPassword = document.getElementById("oldPassword");
        this.newPassword = document.getElementById("newPassword");
        this.confirm = document.getElementById("confirm");
    }
    changePassword = (event) => {
        event.preventDefault();
        var user = {};
        if (this.changeStatus(false)) {
            this.changeStatus(true,"limegreen", false,"Changing password...");
                axios.get(`${this.baseUrl}/login/${this.email.value}&${CryptoJS.MD5(this.oldPassword.value).toString()}`)
                .then(
                  response => {
                    if (response.status === 200) 
                    {
                        user = response.data[0];
                        axios.put(`${this.baseUrl}/user/${user.userId}/updatePassword/${CryptoJS.MD5(this.newPassword.value).toString()}`)
                        .then(() =>
                        {
                            this.changeStatus(true,"limegreen", false,"Password changed Successfully.");
                            this.oldPassword.value = "";
                            this.newPassword.value = "";
                        }).catch((error) => {
                            this.changeStatus(true,"red", false,"Unexpected error, please try again.");
                            this.oldPassword.focus();
                        })
                    }
                  })
                .catch((error) => {
                    this.changeStatus(true,"red", false,"Invalid password, please enter Current password.");
                    this.oldPassword.value = "";
                    this.newPassword.value = "";
                    this.oldPassword.focus();
                })
        }
        else
        {
            if(this.oldPassword.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter Current password.");
                this.oldPassword.focus();
            }  
            else if(this.newPassword.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter New password.");
                this.newPassword.focus();
            }
            else
            {
                this.changeStatus(true,"red", false,"Confirm password change.");
                this.checkBox.focus();
            }

        }
    }
    changeStatus (change,colour, checked, text){
        if(change)
        {
            this.confirm.style="color:"+colour;
            this.confirm.textContent = text;
            this.checkBox.checked = checked;
        }
        else return (this.checkBox.checked &&  this.email.value !=="" && this.oldPassword.value !== "" && this.newPassword.value !=="");
    }
    render() {
        return (
                <div className="col m-b-50">
                    <form onSubmit={this.changePassword}  className="login100-form validate-form flex-sb flex-w">
                        <span className="login100-form-title p-b-32">
                            Change Password
                        </span>
                        <span id="loginEmailLabel" className="txt1 p-b-11">
                        Email
                        </span>
                        <div className="wrap-input100 validate-input m-b-12" data-validate="Email is required" >
                            <input className="input100" id="email" type="email" name="email" placeholder="Enter Email" value = {this.props.email} readOnly disabled style = {{backgroundColor : "lightgray"}}/>
                            <span className="focus-input100"></span>
                        </div>
                        <span id="loginEmailLabel" className="txt1 p-b-11">
                        Current Password
                        </span>
                        <div className="wrap-input100 validate-input m-b-12" data-validate="Password is required">
                            <input className="input100" id="oldPassword" type="password" name="password" placeholder="Enter password" onChange = {this.track}/>
                            <span className="focus-input100"></span>
                        </div>
                        <span id="loginEmailLabel" className="txt1 p-b-11">
                        New Password
                        </span>
                        <div className="wrap-input100 validate-input m-b-12" data-validate="Password is required">
                            <input className="input100" id="newPassword" type="password" name="password" placeholder="Enter new password" onChange = {this.track}/>
                            <span className="focus-input100"></span>
                        </div>
                        <div>
                            <input className="input-checkbox100" id="check" type="checkbox" name="correct-info"/>
                            <label className="label-checkbox100" htmlFor="check">
                            <span className="txt p-b-11" id = "confirm">Confirm password change.</span>
                            </label>
                        </div>
                        <div>
                            <div className="container-login100-btn m-t-20" data-validate="Ceck is required">
                                <button id="btnLogin" className="login100-form-btn" type="submit">
                                Change Password
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
        );   
    }
}

export default ChangePassword;

