import React, { Component } from "react";
import Flag from './Flag';
import axios from 'axios';
class Cvrow extends Component {
  state = {
    test: ''
  }
  constructor(props) {
    super(props);
    this.downloadUrl = 'https://qacvmanager.azurewebsites.net/api/cv/' + props.cvId + '/download';
    this.downIco = "https://image.flaticon.com/icons/svg/138/138601.svg"
    this.deleteUrl = 'https://qacvmanager.azurewebsites.net/api/cv/' + props.cvId + '/delete';
    this.delIco = "https://image.flaticon.com/icons/png/128/138/138767.png"
  }
  deleteCV = (e) => {
    e.preventDefault();
    this.changeStatus("limegreen","Deleting file...");
    axios.get(this.deleteUrl).then(() => {
    this.changeStatus("limegreen","Deleted.");
      this.handleCV(e);
    }).catch((error) => {
      this.changeStatus(true,"red", "Unable to delete file.");
    })
  }

  handleCV = (event) => {
    event.preventDefault();
    axios.get(`https://qacvmanager.azurewebsites.net/api/user/${this.props.user.userId}/cv`)
      .then(response => {
        this.props.onClick(response.data);
      })
  };
  componentDidMount()
  {
      this.confirm = document.getElementById("delete"+this.props.cvId);
      this.confirm.focus();
  }
  track = (event) => {
    this.changeStatus("gray","");
  }
  changeStatus (colour, text){
    this.confirm.style="color:"+colour;
    this.confirm.textContent = text;
  }
  render() {
    return (
      <tr data-index={this.props.userId} >
        {/* <td className="cell">{this.props.user.userId}</td>  */}
        <td>{this.props.fileName}</td>
        <td><Flag changed={this.testState} userId={this.props.user.userId} viewerId={this.props.viewerId} cvId={this.props.cvId} status={this.props.status} findCv={this.findCv} /></td>
        <td><a href={this.downloadUrl}><img className="login100-form-btn-cv" src={this.downIco} alt="Download"  /></a></td>
        <td><a onClick={this.deleteCV}><img className="login100-form-btn-cv"src={this.delIco} alt="Delete" /></a>
          <div>
            <span id = {"delete"+this.props.cvId}  className="txt p-b-11"></span>
          </div>
        </td>
      </tr>

    );
  }
}
export default Cvrow;

