import React, { Component } from "react";
//import profilePic from '/defaultProfile.png'
class InfoBar extends Component {

	constructor(props) {
		super(props);
		
        this.baseUrl = "https://qacvmanager.azurewebsites.net/api";
		this.picUrl = this.updateProfilePic();
	}
	updateProfilePic()
	{
		if(this.props.picture === null)
		{
			return "https://image.flaticon.com/icons/svg/149/149071.svg";
		}
		else
		return (`${this.baseUrl}/user/${this.props.userId}/picture/download?t=${new Date()}`);
	}
	render() {
		if (this.props.depId === 3) {
			return (
				<div>
					<div className="profile-sidebar">
						<div   className="profile-userpic">
							<img id ="pic" src={this.picUrl} width="50" className="img-responsive" alt=""/>
						</div>
						<div className="profile-usertitle">
							<div className="profile-usertitle-name">{this.props.firstName} {this.props.lastName}<br />
								{this.props.role}</div>
							<div className="profile-usertitle-status"><span className="indicator label-success"></span>Online</div>
						</div>
						<div className="clear"></div>
					</div>
					<ul className="nav menu">
						<li><a href="javascript:window.location.replace('#/user')"><em className="fa fa-dashboard">&nbsp;</em> Dashboard</a></li>
						<li><a href="#"><em className="fa fa-calendar">&nbsp;</em> Timetable</a></li>
						<li><a href="javascript:window.location.replace('#/create')"><em className="fa fa-user-plus">&nbsp;</em> Create User</a></li>
						<li><a href="javascript:window.location.replace('#/updateinfo')"><em className="fa fa-user-plus">&nbsp;</em>Update Info</a></li>
						<li><a href="/"><em className="fa fa-sign-out">&nbsp;</em> Logout</a></li>
					</ul>
				</div>
			);
		} else {
			return (
				<div>
					<div className="profile-sidebar">
						<div className="profile-userpic">
						<img id ="pic" src={this.picUrl} width="50" className="img-responsive" alt=""/>
						</div>
						<div className="profile-usertitle">
							<div className="profile-usertitle-name">{this.props.firstName} {this.props.lastName}<br />
								{this.props.role}</div>
							<div className="profile-usertitle-status"><span className="indicator label-success"></span>Online</div>
						</div>
						<div className="clear"></div>
					</div>
					<ul className="nav menu">
						<li><a href="javascript:window.location.replace('#/user')"><em className="fa fa-dashboard">&nbsp;</em> Dashboard</a></li>
						<li><a href="#"><em className="fa fa-calendar">&nbsp;</em> Timetable</a></li>
						<li><a href="javascript:window.location.replace('#/updateinfo')"><em className="fa fa-user-plus">&nbsp;</em>Update Info</a></li>
						<li><a href="/"><em className="fa fa-sign-out">&nbsp;</em> Logout</a></li>
					</ul>
				</div>
			);
		}
	}
}

export default InfoBar;
