import React from "react";
import axios from "axios";
import CryptoJS from "crypto-js";
class ResetPassword extends React.Component {
    track = (event) => {
        this.changeStatus(true, "gray", false,"Confirm password Reset.");
    }
    componentDidMount()
    {
        this.baseUrl = "https://qacvmanager.azurewebsites.net/api";
        this.checkBox = document.getElementById("checkReset");
        this.email = document.getElementById("emailReset");
        this.confirm = document.getElementById("confirmReset");
    }
    resetPassword = (event) => {
        event.preventDefault();
        var user = {};
        if (this.changeStatus(false)) {
            this.changeStatus(true,"limegreen", false,"Reseting password...");
            axios.get(`${this.baseUrl}/email/${this.email.value}`)
            .then(
              response => {
                if (response.data.length > 0) 
                {
                    user = response.data[0];
                    var newPassword = this.generatePassword();
                    axios.put(`${this.baseUrl}/user/${user.userId}/updatePassword/${CryptoJS.MD5(newPassword).toString()}`)
                    .then(() =>
                        {
                            var data = {
                                "from": {"email" : "support@qacvs.com"},
                                "personalizations" : [{"to": [{"email":`${user.email}`, "name":`${user.firstName}+" "+${user.lastName}`}]}],
                                "subject" : "QACVs Password Reset",
                                "content" : [{"type": "text/plain","value": `Hello ${user.firstName}, A request has been received to change the password for your QACVs account.
                                    \nThis is your new password: ${newPassword}\n Please change it once you login \nThank you,
                                    The QACVs Team.`}]
                            };
                            var header = {"Authorization": "Bearer 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",
                            "Content-Type" : "application/json" };
                            axios.post("https://cors-anywhere.herokuapp.com/"+"https://api.sendgrid.com/v3/mail/send", data,{headers : header}).then((response)=>
                            {
                                this.changeStatus(true,"limegreen", false,"Password reset instructions were sent to "+this.email.value+ ".");
                                this.email.value = "";
                            }).catch((error) => {
                                this.changeStatus(true,"red", false,"Unable to send email, please try again.");
                                this.email.focus();
                            })
                        }) 
                }
                else
                {
                    this.changeStatus(true,"red", false,"Invalid Email, please try again."); 
                }
              }).catch((error) => {
                this.changeStatus(true,"red", false,"Unexpected error occurred, please try again.");
                this.email.focus();
            })
         }
         else
         {
            if(this.email.value === "")
            {
                this.changeStatus(true,"red", false,"Please enter Email.");
                this.email.focus();
            } 
            else
            {
                this.changeStatus(true,"red", false,"Confirm password Reset.");
                this.checkBox.focus();
            }
         }
    }
    changeStatus (change,colour, checked, text){
        if(change)
        {
            this.confirm.style="color:"+colour;
            this.confirm.textContent = text;
            this.checkBox.checked = checked;
        }
        else return (this.checkBox.checked && this.email.value!=="");
    }
    generatePassword = (e) => {
        var length = 8,
            charset = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789",
            retVal = "";
        for (var i = 0, n = charset.length; i < length; ++i) {
            retVal += charset.charAt(Math.floor(Math.random() * n));
        }
        return retVal;
    }
    render() {
            return (
                <div className="container-login100">
                <div className="wrap-login100 p-l-85 p-r-85 p-t-55 p-b-55">
                        <form onSubmit={this.resetPassword} className="login100-form validate-form flex-sb flex-w">
                            <span className="login100-form-title p-b-32">
                                Reset Password
                            </span>
                            <span id="loginEmailLabel" className="txt1 p-b-11">
                                Email
                            </span>
                            <div  className="wrap-input100 validate-input m-b-12" data-validate="Email is required">
                                <input className="input100" id="emailReset" type="email" name="email" placeholder="Enter Email" onChange = {this.track}/>
                                <span className="focus-input100"></span>
                            </div>
                            <div  className="flex-sb-m w-full p-b-48" >
                                <div  className="contact100-form-checkbox">
                                    <input className="input-checkbox100" id="checkReset" type="checkbox" name="correct-info" />
                                    <label className="label-checkbox100" htmlFor="checkReset">
                                        <span className="txt p-b-11" id = "confirmReset">Confirm password Reset.</span>
                                    </label>
                                </div>
                            </div>
                            <div className="container-login100-form-btn" data-validate="Ceck is required">
                                <button id="btnLogin" className="login100-form-btn" type="submit">
                                Reset
                                </button>
                            </div>
                            
                        </form>
                    </div>
                </div>
            );
    }
}

export default ResetPassword;

