import React from 'react';
import LogIn from './LogIn.js'
import User from './User.js'
import { Route,NotFound, Switch, HashRouter } from "react-router-dom";
import axios from 'axios'
import CreatePage from './CreatePage.js'
// // import "./style/index2.css";
 import ResetPassword from './ResetPassword.js'
// import CreateAccount from './CreateAccount.js'
import UpdateInfoPage from './UpdateInfoPage.js'
import NavBar from './NavBar'
import InfoBar from './InfoBar.js'
class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loginInfo: [],
      cvs: [],
      rows: [],
      visibility: 'hidden'
    };
    this.baseState = this.state;
  }
  findUser = (userInfo) => {
    this.setState(prevState => ({
      rows: userInfo
    }));
  }
  findCv = (userCV) => {
    this.setState(prevState => ({
      cvs: userCV
    }));
    this.setState(prevState => ({
      visibility: 'visible'
    }));
    
  }
  resetCV = (info) => {
    this.setState(prevState => ({
      cvs: info
    }));
  }
  logIn = (userInfo) => {
    this.setState(prevState => ({
      loginInfo: userInfo
    }));
  }
  refreshCV = () => {
    this.setState(prevState => ({
    }));
  }
 showNavBar = () =>
  {
    if (this.state.loginInfo[7] !== undefined && this.state.loginInfo[7] !== null)
    {
      return (<div>
          <NavBar/>
          <div id="sidebar-collapse" className="col-sm-3 col-lg-2 sidebar">
            <Route component={(props) => <InfoBar {...props}  userId={this.state.loginInfo[0]}
              firstName={this.state.loginInfo[1]}
              lastName={this.state.loginInfo[2]}
              role={this.state.loginInfo[7].role}
              onSubmit={this.state.findUser}
              depId={this.state.loginInfo[7].departmentId}
            picture = {this.state.loginInfo[5]} 
            findUser={this.findUser}  />} />
          </div>
      </div>)
    }
    else
    return(null)

  }
  showLogin = () =>
  {
    if (this.state.loginInfo[7] !== undefined && this.state.loginInfo[7] !== null)
    {
      return (<Route component={(props) => <User {...props} 
      loginInfo={this.state.loginInfo} rows={this.state.rows} 
      cvs={this.state.cvs} resetCV={this.resetCV} findUser={this.findUser} 
      findCv={this.findCv} visibility={this.state.visibility} />} />)
    }
    else
    return (<Route component={(props) => <LogIn {...props} onSubmit={this.logIn} loginInfo={this.state.loginInfo} />} />);

  }
  footer = () =>
  {
    return (<div className="col-sm-9 col-sm-offset-3 col-lg-10 col-lg-offset-2 main"><p className="back-link">Group project: <a href="#">CV-Bunny</a></p> </div>);

  }
  render() {
      return (
      <HashRouter>
        <div>
          <div className="container-fluid"  style={{"minWidth": "500px"}}>
            <Route component ={this.showNavBar}/>
              <Switch>
                      {/* <Route  exact path="/" render={(props) => <LogIn {...props} onSubmit={this.logIn} loginInfo={this.state.loginInfo} />} /> */}
                      <Route exact  path='/user' render={(props) => <User {...props} 
                      loginInfo={this.state.loginInfo} rows={this.state.rows} 
                      cvs={this.state.cvs} resetCV={this.resetCV} findUser={this.findUser} 
                      findCv={this.findCv} visibility={this.state.visibility} />} />
                      <Route exact path='/create' render={(props) => <CreatePage {...props} 
                      loginInfo={this.state.loginInfo} />} />
                      <Route exact  path='/updateinfo' render={(props) => <UpdateInfoPage {...props} 
                      loginInfo={this.state.loginInfo} />} />
                      <Route component ={this.showLogin}/>
              </Switch>
          </div>
        <Route component = {this.footer} />
        </div>
      </HashRouter>
      
      );
    }
   
}
export default Main;