import React, { Component } from "react";
import axios from 'axios';
class UploadPhoto extends Component {

   
    track = (event) => {
        this.changeStatus(true, "gray", false,"Confirm Upload.");
    }
    componentDidMount()
    {
        this.checkBox = document.getElementById("checkUp");
        this.cvFile = document.getElementById("file");
        this.confirm = document.getElementById("confirmUp");
        this.profilepic = document.getElementById("profilepic");
        this.pic = document.getElementById("pic");
    }
   constructor(props) {
        super(props);
        this.baseUrl = "https://qacvmanager.azurewebsites.net/api";
		this.picUrl = this.updateProfilePic();
        this.uploadCv = (e) => {
            e.preventDefault();
            if (this.changeStatus(false)) {
                this.changeStatus(true, "limegreen", false,"Uploading file...");
                var cvName = this.cvFile.files[0].name;
                var setPostLink = this.baseUrl+"/user/" +this.props.userId + "/picture/";
                var formData = new FormData();
                 formData.append("file", this.cvFile.files[0]);

               var req = axios.post(setPostLink, formData,
                {
                  headers: {
                        'Content-Type': 'multipart/form-data'
                      }
                });
                req.then(response => {
                    if (response.status === 200) 
                    {
                        this.changeStatus(true, "limegreen", false,cvName+" uploaded Successfully.");
                        this.cvFile.value = null;
                        this.profilepic.src = this.pic.src =`${this.baseUrl}/user/${this.props.userId}/picture/download?t=${new Date()}`;
                    }
                })
            }
            else
            {
                if(this.cvFile.files[0] === undefined)
                {
                    this.changeStatus(true,"red", false,"Please Choose a File.");
                    this.cvFile.focus();
                } 
                else
                {
                    this.changeStatus(true,"red", false,"Confirm Upload.");
                }
            }
        }
    }
	updateProfilePic()
	{
		if(this.props.picture === null)
		{
			return "https://image.flaticon.com/icons/svg/149/149071.svg";
		}
		else
		return (`${this.baseUrl}/user/${this.props.userId}/picture/download?t=${new Date()}`);
	}
  changeStatus (change,colour, checked, text){
        if(change)
        {
            this.confirm.style="color:"+colour;
            this.confirm.textContent = text;
            this.checkBox.checked = checked;
        }
        else return (this.checkBox.checked && this.cvFile.files[0]!==undefined);
    }
  render() {
        return ( 
        <div className="wrap-input100">
            <div className="wrap p-l-85 p-r-85 p-t-55 p-b-55">
                <div className="row m-b-12">
                    <img className = "col" id ="profilepic" alt = "lol" src = {this.picUrl}  className="img-responsive"/>
                </div>
                <div className="validate-input m-b-12 login100-form-btn" data-validate="Picture is required" >
                    <input className="input100" id="file"type="file" name="file"onChange = {this.track}/>
                    <span className="focus-input100"></span>
                </div>
                <div>
                    <input  className="input-checkbox100" id="checkUp" type="checkbox" name="correct-info" />
                    <label className="label-checkbox100" htmlFor="checkUp">
                            <span className="lnr lnr-envelope" id = "confirmUp">Confirm Upload.</span>
                    </label>
                </div>
                <div className="container-login100-btn m-t-20" data-validate="Ceck is required">
                    <button    className="login100-form-btn" onClick = {this.uploadCv}>Upload</button>
                </div>
            </div>
        </div>
        );
  }
}
export default UploadPhoto;
